import React,{ Component } from "react";
import Button from '../../../Components/UI/Button/Button';
import classes from './ContactData.css';
import axios from './../../../Axios-Orders';
import Spinner from '../../../Components/UI/Spinner/Spinner';
import Input from './../../../Components/UI/Input/Input';
import {connect} from 'react-redux';
import withErrorHandler from '../../../hoc/WithErrorHandler/WithErrorHandler';
import * as actions from '../../../Store/actions/index';
import {updateObject} from '../../../Shared/utility';
class ContactData extends Component{
    state = {
        orderForm:{
            name: {
                elementType: 'input',
                elementConfig: {
                    type: 'text',
                    placeholder: 'Your Name'
                },
                value: '',
                validation: {
                    required: true
                },
                valid: false,
                touched: false
            },
                street:{
                    elementType: 'input',
                    elementConfig: {
                        type: 'text',
                        placeholder: 'Street'
                    },
                    value: '',
                    validation: {
                        required: true
                    },
                    valid: false,
                    touched: false
                } ,
                zipCode: {
                    elementType: 'input',
                    elementConfig: {
                        type: 'text',
                        placeholder: 'Your ZIPCODE'
                    },
                    value: '',
                    validation: {
                        required: true,
                        minLength: 5,
                        maxLength: 9,
                        isNumeric: true
                    },
                    valid: false,
                    touched: false
                },
                country: {
                    elementType: 'input',
                    elementConfig: {
                        type: 'text',
                        placeholder: 'Your Country'
                    },
                    value: '',
                    validation: {
                        required: true
                    },
                    valid: false,
                    touched: false
                },
            email: {
                elementType: 'input',
                elementConfig: {
                    type: 'text',
                    placeholder: 'Your E-Mail'
                },
                value: '',
                validation: {
                    required: true,
                    isEmail: true
                },
                valid: false,
                touched: false
            }
        ,
        deliveryMethod: {
            elementType: 'select',
            elementConfig: {
                options: [{value: 'fastest', displayValue: 'fastest'},
                {value: 'cheapest', displayValue: 'Cheapest'}
            ]
            },
            value: '',
            validation: {},
            valid: true
         }
    },
    formIsValid: false
    }

    orderHandler = (event) => {
        event.preventDefault();
           this.setState( { spinning: true } );
           const formdata = {};
           for(let formElementIdentifier in this.state.orderForm){
               formdata[formElementIdentifier] = this.state.orderForm[formElementIdentifier].value;

           }
        const order = {
             ingredients: this.props.ingredients,
             price: this.props.price,
             OrderData: formdata,
             userId: this.props.userId
         }
        this.props.onOrderHandler(order,this.props.token);
    }

    checkValidity = (value,rules) =>{
        let isValid = true;
        if(rules.required){
            isValid = value.trim() !== '' && isValid;
        }
        if(rules.minLength){
            isValid = value.length >= rules.minLength && isValid;   
        }
        if(rules.maxLength){
            isValid = value.length <= rules.maxLength && isValid; 
        }
        if (rules.isEmail) {
            const pattern = /[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?/;
            isValid = pattern.test(value) && isValid
        }

        if (rules.isNumeric) {
            const pattern = /^\d+$/;
            isValid = pattern.test(value) && isValid
        }
        return isValid;
    }

    InputHandler = (event,inputIndentifier) => {
            const UpdatedFormElement = updateObject(this.state.orderForm[inputIndentifier],{
                value:  event.target.value,
                valid:  this.checkValidity(event.target.value,this.state.orderForm[inputIndentifier].validation),
                touched: true

            });
            const UpdatedForm = updateObject(this.state.orderForm,{
                [inputIndentifier]: UpdatedFormElement
            });
            this.setState({orderForm: UpdatedForm});
            let formIsValid = true;
            for(let inputIdentifier in UpdatedForm){
                formIsValid = UpdatedForm[inputIdentifier].valid && formIsValid;
            }
            this.setState({orderForm: UpdatedForm, formIsValid: formIsValid});
    }
    render(){
        const formElementArray = [];
        for(let key in this.state.orderForm){
            formElementArray.push({
                id: key,
                config: this.state.orderForm[key]
            });
        }
        let form = (<form>
                    {formElementArray.map(formElement => (
                        <Input elementType = {formElement.config.elementType}
                        invalid = {!formElement.config.valid}
                        shouldValidate = {formElement.config.validation}
                        elementConfig = {formElement.config.elementConfig}
                        touched = {formElement.config.touched}
                        value = {formElement.config.value} key = {formElement.id} changed = {(event) => this.InputHandler(event,formElement.id)}/>
                    ))}
                <Button btnType = 'Success' clicked = {this.orderHandler} disabled = {!this.state.formIsValid}>ORDER</Button>
                </form>);
        if(this.props.loading){
            form = <Spinner />;
        }
        return(
            <div className = {classes.ContactData}>
                <h4>Enter Your Details Here</h4>
                {form}
            </div>
        );
    }
}

const MapStateToProps = state => {
    return{
        ingredients: state.burgerBuilder.ingredients,
        price: state.burgerBuilder.totalPrice,
        loading: state.order.loading,
        token: state.auth.token,
        userId: state.auth.userId
    }
}
const MapDispatchToState = dispatch => {
    return{
        onOrderHandler: (orderData,token) => dispatch(actions.PurchaseBurger(orderData,token)) 
    }
}
export default connect(MapStateToProps,MapDispatchToState)(withErrorHandler(ContactData,axios));


