import React from 'react';
import classes from './Input.css';

const Input = (props) => {

    let inputElement = null;
    let inputClasses =[classes.InputElement]
    if(props.invalid && props.shouldValidate && props.touched){
        inputClasses.push(classes.Invalid);
    }
    switch(props.elementType){
        case('input'):
            inputElement = <input type = 'text' className = {inputClasses.join(' ')} {...props.elementConfig} value = {props.value}  onChange = {props.changed}/> ;
            break;
        case('textarea'):
            inputElement = <input type = 'textarea' className = {inputClasses} {...props.elementConfig} value = {props.value} onChange = {props.changed} />;
            break;
        case('select'):
            inputElement = <select  
            className = {inputClasses}  
            value = {props.value}   onChange = {props.changed} >{props.elementConfig.options.map(option => (
                    <option key = {option.value} value = {Option.value}>{option.displayValue}</option>
                ))}</select>
            break;
        default:
            inputElement = <input type = 'text' className = {inputClasses} {...props.elementConfig} value = {props.value}  onChange = {props.changed}/>;
    }

    return(
        <div className = {classes.Input}>
        <label className = {classes.Label}>{props.label}</label>
            {inputElement}
    
        </div>
    )

}

export default Input;


